export const convertMapData = (mapPopUp, map) => {
  let data = [];
  for (var cityName in mapPopUp) {
    let mapPopUpValue;
    if (cityName === "省直部属") {
      mapPopUpValue = map[cityName];
    } else if (cityName === "流动站") {
      mapPopUpValue = map[cityName];
    } else {
      mapPopUpValue = map[cityName]; //这里修改了，变成三个字
    }
    data.push({
      name: cityName,
      value: mapPopUp[cityName].concat(mapPopUpValue),
    });
  }
  return data;
};
