        let rawChartDataKey = JSON.parse(key);
        let obj = {};
        obj.name = rawChartDataKey[1];
        let rawChartvalue = rawChartData[key];
        if (rawChartDataKey[0] == "nation") {
          obj["国家级博士后科研工作站"] = rawChartvalue;
        } else if (rawChartDataKey[0] == "province") {
          obj["浙江省级博士后工作站"] = rawChartvalue;
        }
        chartData.push(obj);
      }
      return chartData;
