export const getMapOption = (mapTitle, mapName, mapPopUpData) => {
  let mapOption = {
    title: {
      text: mapTitle,
      subtext: "",
      left: "center",
      top: "5%",
      textStyle: {
        color: "#A4C338",
        fontSize: 18,
        fontFamily: "微软雅黑",
        fontWeight: "bold",
      },
    },
    tooltip: {
      trigger: "item",
      formatter: function (params) {
        let cityNum = params.data.value[2];
        let cityName = params.name;
        return cityName + " : " + cityNum;
      },
    },
    geo: {
      map: "HZ",
      show: true,
      roam: false,
      left: "center",
      select: {
        label: {
          show: true,
          color: "#fff",
        },
      },
      label: {
        normal: {
          show: true,
          textStyle: {
            color: "#fff",
          },
        },
        emphasis: {
          show: true,
          textStyle: {
            color: "#fff",
          },
        },
      },
      itemStyle: {
        normal: {
          areaColor: "#3a7fd5",
          borderColor: "#0a53e9",
          shadowColor: "#092f8f",
          shadowBlur: 20,
        },
        emphasis: {
          areaColor: "#0a2dae",
        },
      },
    },
    series: [
      {
        type: "map",
        map: mapName,
        geoIndex: 0,
        data: mapPopUpData,
      },
      {
        name: "pin",
        type: "scatter",
        data: mapPopUpData,
        coordinateSystem: "geo",
        symbol: "pin",
        symbolSize: [50, 50],
        label: {
          normal: {
            show: true,
            textStyle: {
              color: "#fff",
              fontSize: 9,
            },
            formatter(value) {
              let mapPopUpValue = value.data.value[2];
              return mapPopUpValue;
            },
          },
        },
        itemStyle: {
          normal: {
            color: "#ffcc00",
          },
        },

        showEffectOn: "render",
        rippleEffect: {
          brushType: "stroke",
        },
        hoverAnimation: true,
        zlevel: 1,
      },
    ],
  };
  return mapOption;
};
