<script>
import * as echarts from "echarts";
export default {
  name: "AppHeader",
  props: ["appHeaderOption"],
  mounted() {
    let myChart = echarts.init(document.getElementById("title"));
    myChart.setOption(this.appHeaderOption);
    window.addEventListener("resize", function () {
      myChart.resize();
    });
  },
};
</script>

<template>
  <div class="index-title">
    <i class="index-title-left">
      <img src="../assets/title-left.png" alt="tag-left" />
    </i>
    <div id="title" :style="{ height: '100%', width: '100%' }"></div>
    <i class="index-title-right">
      <img src="../assets/title-right.png" alt="tag-right" />
    </i>
  </div>
</template>

<style scoped></style>
