export const getChartOption = (
  barColor,
  legendData,
  dimensionData,
  chartData,
  barSeries
) => {
  let chartOption = {
    tooltip: {},
    grid: {
      right: "10%",
      top: "15%",
      height: "70%",
      width: "80%",
    },
    color: barColor,
    legend: {
      data: legendData,
      right: "5%",
      top: "5%",
      textStyle: {
        color: "#fff",
      },
    },
    dataset: {
      dimensions: dimensionData,
      source: chartData,
    },
    yAxis: {
      axisLine: {
        lineStyle: {
          type: "solid",
          color: "#fff",
          width: 0.5,
        },
      },
    },
    xAxis: {
      type: "category",
      axisLine: {
        lineStyle: {
          type: "solid",
          color: "#fff",
          width: 0.5,
        },
      },
      axisLabel: {
        color: "#fff",
        interval: 0,
        align: "center",
        fontSize: 8,
        width: 40,
        //rotate:'0',
        overflow: "break",
      },
    },
    // Declare several bar series, each will be mapped
    // to a column of dataset.source by default.
    series: barSeries,
  };
  return chartOption;
};
