<script>
import * as echarts from "echarts";
import BaseContainer from "../features/BaseContainer.vue";
export default {
  name: "CityTheChart",
  components: { BaseContainer },
  props: ["chartOption", "city"],
  mounted() {
    let Chart = echarts.init(document.getElementById("chart"));
    Chart.setOption(this.chartOption);
    Chart.on("click", () => {
      this.$emit("clickStation");
    });
    window.addEventListener("resize", () => {
      Chart.resize();
    });
  },
};
</script>

<template>
  <base-container
    id="stationChart"
    :ititle="city + '博士后工作站数据'"
    iwidth="52"
    iheight="40"
    hasLink="no"
  >
    <div id="chart"></div>
  </base-container>
</template>

<style>
#ChartBoard {
  position: absolute;
  top: 12%;
  right: 3%;
}
#chart {
  position: absolute;
  top: 7%;
  left: 3%;
  width: 100%;
  height: 100%;
}
</style>
