        "浙江省级博士后工作站",
      ];
      chartSetting["barColor"] = ["#91cc75", "#ED7D31", "#C00571"];
      return chartSetting;
    },
    getShortCityNameToGetData(city) {
      if (!city) return;
      let shortCityNameToGetData = "";
      if (city === "省直部属") {
        shortCityNameToGetData = "省直部属";
      } else if (city === "流动站") {
        shortCityNameToGetData = "流动站";
      } else {
        shortCityNameToGetData = city.slice(0, -1); //获取前两位
      }
      return shortCityNameToGetData;
    },
    getChartData(rawChartData) {
      let chartData = [];
      for (let key in rawChartData) {
        //let rawChartDataKey = JSON.parse(key);
        let rawChartDataKey = key;
        let obj = {};
        obj.name = this.city;
        let rawChartvalue = rawChartData[key];
        if (rawChartDataKey == "nation") {
          obj["国家级博士后科研工作站"] = rawChartvalue;
        } else {
          obj["浙江省级博士后工作站"] = rawChartvalue;
        }
        chartData.push(obj);
      }

      return chartData;
    },
    swtichToCity(val) {
