<script>
import * as echarts from "echarts";

export default {
  name: "TheMap",
  props: ["mapOption", "mapGeoJson"],
  mounted() {
    console.log(this.mapOption);
    echarts.registerMap("HZ", this.mapGeoJson);
    let myChart = echarts.init(document.getElementById("map"));
    myChart.setOption(this.mapOption);
    myChart.off("click");
    //使用箭头函数就不存在this绑定错误的问题，那么为什么呢？
    myChart.on("click", (params) => {
      this.$emit("swtichToCity", params); //用that.$emit即可解决传值问题
    });
    window.addEventListener("resize", () => {
      myChart.resize();
    });
  },
};
</script>

<template>
  <div id="map"></div>
</template>

<style>
#map {
  position: absolute;
  top: 12%;
  left: 3%;
  width: 40%;
  height: 85%;
}
</style>
