<script>
import BaseContainer from "../features/BaseContainer.vue";
import * as echarts from "echarts";
export default {
  name: "DataStatistic",
  props: ["countByType"],
  components: { BaseContainer },
  data() {
    return {
      option: {
        tooltip: {
          trigger: "item",
        },
        legend: {
          orient: "vertical",
          right: "10%",
          top: "15%",
          textStyle: {
            color: "#fff",
          },
        },
        series: [
          {
            name: "载体统计",
            type: "pie",
            radius: "90%",
            top: "10%",
            right: "40%",
            data: [
              { value: 0, name: "国家级工作站" },
              { value: 0, name: "浙江省级工作站" },
              { value: 0, name: "流动站" },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  position: "inner",
                  formatter: "{c} ",
                },
              },
            },
          },
        ],
      },
    };
  },
  mounted() {
    let myChart = echarts.init(document.getElementById("entityChart"));
    let type = this.option.series[0].data;
    console.log(type);
    type[0].value = this.countByType["nation"];
    type[1].value = this.countByType["province"];
    type[2].value = this.countByType["3"] ? this.countByType["3"] : 0;
    myChart.setOption(this.option);
    myChart.on("click", (res) => {
      if (res.name == "流动站") {
        window.open("http://zjbsh.zjhwrc.com/posts/153");
      }
    });
    window.addEventListener("resize", function () {
      myChart.resize();
    });
  },
};
</script>

<template BaseContainer>
  <base-container
    ititle="浙江省博士后载体"
    iwidth="20"
    iheight="19"
    hasLink="no"
  >
    <div id="entityChart" :style="{ height: '80%', width: '100%' }"></div>
  </base-container>
</template>
<style scoped>
#entityChart {
  position: absolute;
  width: 100%;
  height: 100%;
}
</style>
